import React from "react"
import "./CSS/skill-block.css"

function Skills(props) {
    const styles = {
        background: props.color
    }

    function raisingScale(e) {
        e.target.style.transform = "scale(1.03)"
        e.target.style.boxShadow = "0px 0px 5px 1px black"
        e.target.style.transition = "all 0.3s ease-out"
    }

    function reducingScale(e) {
        e.target.style.transform = "scale(1.00)"
        e.target.style.boxShadow = "0 0 0 0 black"
        e.target.style.transition = "all 0.3s ease-out"
    }

    return (
        <div className="skill-block" style={styles}>
            <div className="block-inside">
                <h1 className="skill-name">{props.skill}</h1>
                <p className="skill-description">
                    {props.description}</p>
                <img
                    src={props.pic}
                    className="skill-picture"
                    onMouseOver={raisingScale}
                    onMouseOut={reducingScale}/>
            </div>
        </div>
    )
}

export default Skills