import React, {Component} from "react"
import './App.css';
import Questions from "./Components/questions"
import Header from "./Components/Header"

class App extends React.Component {
    state = {
        firstName: "Гость",
        click: ""
    }

    updateData = (value) => {
        !value
            ? this.setState({firstName: "Гость"})
            : this.setState({firstName: value})
    }

    render() {
        return (
            <div>
                <Header/>
                <Questions updateData={this.updateData} updateClass={this.updateClass}/>
            </div>
        )
    }
}

export default App;
