import React, {Component} from "react"
import "./CSS/Morse-block.css"

class MorseBlock extends Component {
    constructor() {
        super()
        this.state = {
            letters: ""
        }
        this.Morse = this
            .Morse
            .bind(this)
    }

    Morse(event) {
        console.log(event.target.value) //Почему если сделать this.state.value значения будут приходить с задержкой на один символ
        this.setState({letters: event.target.value})
        let val = event.target.value
        let words = []
        let letters = []
        let translation = ""
        let alpabet = []

        alpabet = ["А", "Б", "В", "Г", "Д", "Е", "Ё", "Ж", "З", "И", "Й", "К", "Л", "М", "Н", "О", "П", "Р", "С", "Т", "У", "Ф", "Х", "Ц", "Ч", "Ш", "Щ", "Ъ", "Ы", "Ь", "Э", "Ю", "Я", "а", "б", "в", "г", "д", "е", "ё", "ж", "з", "и", "й", "к", "л", "м", "н", "о", "п", "р", "с", "т", "у", "ф", "х", "ц", "ч", "ш", "щ", "ъ", "ы", "ь", "э", "ю", "я", "   ", ".", ",", "—", "!", "?", "1", "2", "3", "4", "5", "6", "7", "8", "9", "0"];
        
        var language = function (text) {
            return /[a-z]/i.test(text)
        }
        
        if (language(val)) {
            alpabet = ["A", "B", "W", "G", "D", "E", "V", "Z", "I", "J", "K", "L", "M", "N", "O", "P", "R", "S", "T", "U", "F", "H", "C", "", "", "Q", "Y", "X", "", "", "", "a", "b", "w", "g", "d", "e", "v", "z", "i", "j", "k", "l", "m", "n", "o", "p", "r", "s", "t", "u", "f", "h", "c", "", "", "q", "y", "x", "", "", "", "   ", ".", ",", "—", "!", "?", "1", "2", "3", "4", "5", "6", "7", "8", "9", "0"];
        };
        
        let Morse = [" · − ", " − · · · ", " · − − ", " − − · ", " − · · ", " · ", " · · · − ", " − − · · ", " · · ", " · − − − ", " − · − ", " · − · · ", " − − ", " − · ", " − − − ", " · − − · ", " · − · ", " · · · ", " − ", " · · − ", " · · − · ", " · · · · ", " − · − · ", " − − − · ", " − − − − ", " − − · −", " − − · − − ", " − · − − ", " − · · − ", " · · − · · ", " · · − − ", " · − · − ", " · − ", " · − ", " − · · · ", " · − − ", " − − · ", " − · · ", " · ", " · · · − ", " − − · · ", " · · ", " · − − − ", "  − · − ", " · − · · ", " − − ", " − · ", " − − − ", " · − − · ", " · − · ", " · · · ", " − ", " · · − ", " · · − · ", " · · · · ", " − · − · ", " − − − · ", " − − − − ", " − − · − ", " − − · − − ", " − · − − ", " − · · − ", " · · − · · ", " · · − − ", " · − · − ", " · − ", "   ", " · · · · · · ", " · − · − · − ", " − · · · · − ", " − − · · − − ", " · · − − · · ", "· − − − −", "· · − − −", "· · · − −", "· · · · −", "· · · · ·", "− · · · ·", "− − · · ·", "− − − · ·", "− − − − ·", "− − − − −"]

        for (let i = 0; i <= val.length - 1; i++) {
            words.push(val[i])
        }
    
        for (let j = 0; j <= words.length - 1; j++) {
            for (let k = 0; k <= alpabet.length - 1; k++) {
                if (words[j] == alpabet[k]) {
                    letters.push(k)
                }
            }
        }
        for (let m = 0; m <= letters.length - 1; m++) {
            let symbol = Morse[letters[m]]
            translation = translation + symbol
        }
        let output = document.getElementById("morse-output")
        output.innerHTML = translation
    }

    render() {
        return (
            <div className="container-morse">
            <h1 className="Morse-header">Азбука Морзе</h1>  
                <input
                    type="text"
                    id="morse-input"
                    placeholder="Введите текст на английском или русском языках"
                    value={this.state.value}
                    onChange={this.Morse}/>
                <div id="morse-output"></div>
            </div>
        )
    }
}

export default MorseBlock