import React, {Component} from "react"
import ReactDOM from "react-dom"
import "./CSS/Header.css"
import "./CSS/Choice-block.css"
import Choice from "./Choise"

class Questions extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            firstName: "Гость"
        }
        this.dataSaver = this
            .dataSaver
            .bind(this)

        this.scrollDown = this
            .scrollDown
            .bind(this)

        this.changingColor = this
            .changingColor
            .bind(this)

        this.changingColorBack = this
            .changingColorBack
            .bind(this)
    }

    changingColor(e) {
        e.target.style.color = "#FF8300"
        e.target.style.transition = "all 0.5s ease-out"
        e.target.style.cursor = "pointer"
        e.target.style.border = "1px solid white"
    }

    changingColorBack(e) {
        e.target.style.color = " black"
        e.target.style.border = "0px solid black"
        e.target.style.transition = "all 0.5s ease-out"
    }

    dataSaver(event) {
        const {name, value} = event.target
        !value
            ? this.setState({firstName: "Гость"})
            : this.setState({firstName: value})
    }

    scrollDown() {
        const choice = document.getElementById("choice-block")
        choice.style.display = "block"
        window.scrollTo({top: 900, behavior: "smooth"})
    }

    render() {
        return (
            <div >
                <div className="questionsBlock">
                    <input
                        type="text"
                        className="inputName"
                        placeholder="Ваше имя"
                        name="firstName"
                        onChange={this.dataSaver}/>
                    <button
                        className="submit"
                        onClick={this.scrollDown}
                        onMouseOver={this.changingColor}
                        onMouseOut={this.changingColorBack}>Принять</button>
                </div>
                <div id="choice-block">
                    <Choice name={this.state.firstName}/>
                </div>
            </div>
        )
    }
}

export default Questions